import React from "react";
import { connect } from "react-redux";

import { ResultLines, StatisticTable } from "../utils";
import { fetchOwner, fetchOwnerStats, queryResults } from "../../actions";

class OwnerDetailPage extends React.Component {
  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.fetchOwner(id);
    this.props.fetchOwnerStats(id);
    this.props.queryResults([
      {
        field: "owner_id",
        operator: "eq",
        arguments: [id],
      },
    ]);
  }

  routeText(route) {
    if (route === 1) {
      return "Route";
    } else {
      return "Sprint";
    }
  }

  winRate() {
    let wins = 0;
    let starts = 0;
    this.props.owner.surface_stats.forEach((surface) => {
      wins += surface.wins;
      starts += surface.starts;
    });
    if (starts > 0) {
      return Math.round((wins * 100) / starts, 2);
    } else {
      return 0;
    }
  }

  render() {
    return (
      <div>
        <div className="ui grid">
          <div className="ui row">
            <div className="ui six wide column">
              <h2 className="ui header left floated">Name</h2>
              <div>{this.props.owner.full_name}</div>
            </div>
            <div className="ui six wide column right floated">
              <div className="ui grid row">
                <h2 className="ui header left floated">Win %</h2>
                <div className="ui right floated content">{this.winRate()}</div>
              </div>
            </div>
          </div>
          <div className="ui four column row">
            <div className="ui column">
              <StatisticTable
                title="Trainer"
                data={this.props.owner.trainer_stats.map((stat) => ({
                  ...stat,
                  feature_name: stat.full_name,
                }))}
              />
            </div>
            <div className="ui column">
              <StatisticTable
                title="Jockey"
                data={this.props.owner.jockey_stats.map((stat) => ({
                  ...stat,
                  feature_name: stat.full_name,
                }))}
              />
            </div>
            <div className="ui column">
              <StatisticTable
                title="Class"
                data={this.props.owner.class_stats.map((stat) => ({
                  ...stat,
                  feature_name: stat.classification,
                }))}
              />
            </div>
            <div className="ui column">
              <StatisticTable
                title="Surface"
                data={this.props.owner.surface_stats.map((stat) => ({
                  ...stat,
                  feature_name: `${stat.surface} - ${this.routeText(
                    stat.route
                  )}`,
                }))}
              />
            </div>
          </div>
        </div>
        <ResultLines
          headers={[
            "track_code",
            "race_date",
            "race_number",
            "distance",
            "surface",
            "program_number",
            "horse_name",
            "trainer",
            "jockey",
          ]}
          results={this.props.results}
        />
      </div>
    );
  }
}

const mapStatetoProps = (state) => {
  return {
    owner: state.backend.owner,
    results: state.backend.results,
  };
};

export default connect(mapStatetoProps, {
  fetchOwner,
  fetchOwnerStats,
  queryResults,
})(OwnerDetailPage);
