import _ from "lodash";

import {
  FETCH_CARDS,
  FETCH_HORSE,
  FETCH_RACE,
  FETCH_TRAINER,
  FETCH_JOCKEY,
  FETCH_OWNER,
  QUERY_RESULTS,
  INDEX_JOCKEYS,
  INDEX_TRAINERS,
  INDEX_TRACKS,
} from "../actions/actionTypes";

const INITIAL_STATE = {
  cards: [],
  race: {},
  trainer: {
    track_stats: [],
    jockey_stats: [],
    class_stats: [],
    surface_stats: [],
  },
  jockey: {
    track_stats: [],
    trainer_stats: [],
    class_stats: [],
    surface_stats: [],
  },
  owner: {
    trainer_stats: [],
    jockey_stats: [],
    class_stats: [],
    surface_stats: [],
  },
  results: [],
  horse: {},
  trainerIndex: [],
  jockeyIndex: [],
  trainerIndex: [],
  trackIndex: [],
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_CARDS:
      return { ...state, cards: action.payload };
    case FETCH_HORSE:
      return { ...state, horse: action.payload };
    case FETCH_RACE:
      return { ...state, race: action.payload };
    case FETCH_TRAINER:
      return { ...state, trainer: { ...state.trainer, ...action.payload } };
    case FETCH_JOCKEY:
      return { ...state, jockey: { ...state.jockey, ...action.payload } };
    case FETCH_OWNER:
      return { ...state, owner: { ...state.owner, ...action.payload } };
    case QUERY_RESULTS:
      return { ...state, results: action.payload };
    case INDEX_JOCKEYS:
      return { ...state, jockeyIndex: action.payload };
    case INDEX_TRAINERS:
      return { ...state, trainerIndex: action.payload };
    case INDEX_TRACKS:
      return { ...state, trackIndex: action.payload };
    default:
      return state;
  }
};
