import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { AttributeFilterModal } from "../utils";
import { fetchCards } from "../../actions";

import "../../styles/styles.css";

class HomePage extends React.Component {
  componentDidMount() {
    return this.props.fetchCards();
  }

  renderedCard(card) {
    const key = card.track_code.toUpperCase() + " - " + card.date;
    return (
      <div className="six wide computer eight wide tablet column" key={key}>
        <div className="race">
          <div className="ui header center aligned">{key}</div>
          <div className="ui content">
            <table className="ui compact basic table">
              <thead>
                <tr>
                  <th>Race</th>
                  <th>Distance</th>
                  <th>Surface</th>
                  <th>Class</th>
                  <th>Purse</th>
                </tr>
              </thead>
              <tbody>
                {card.races.map((race) => {
                  return (
                    <tr key={race.id} className="row-highlight">
                      <td data-label="Race">
                        <Link className="rowLink" to={`/races/${race.id}`}>
                          {race.race_number}
                        </Link>
                      </td>

                      <td data-label="Distance">
                        <Link className="rowLink" to={`/races/${race.id}`}>
                          {race.distance}
                        </Link>
                      </td>

                      <td data-label="Surface">
                        <Link className="rowLink" to={`/races/${race.id}`}>
                          {race.surface}
                        </Link>
                      </td>

                      <td data-label="Class">
                        <Link className="rowLink" to={`/races/${race.id}`}>
                          {race.classification}
                        </Link>
                      </td>

                      <td data-label="Purse">
                        <Link className="rowLink" to={`/races/${race.id}`}>
                          {race.purse}
                        </Link>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }

  renderedCards() {
    return (
      <div className="ui row scroll">
        {this.props.cards.map((card) => {
          return this.renderedCard(card);
        })}
      </div>
    );
  }

  renderedConnections() {
    return (
      <div className="ui three column row">
        <div className="column">
          <div className="ui card">
            <div className="ui header">Trainers</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
          </div>
        </div>
        <div className="column">
          <div className="ui card">
            <div className="ui header">Jockeys</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
          </div>
        </div>
        <div className="column">
          <div className="ui card">
            <div className="ui header">Combo</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
            <div className="content">line</div>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div>
        <div className="ui relaxed grid">
          <div className="ui row">
            <div className="left floated eight wide column">
              <h2 className="ui header">Recent Cards</h2>
            </div>
            <span className="four wide column" />
            <div className="right floated four wide column">
              <AttributeFilterModal label="Select Tracks" />
            </div>
          </div>
          {this.renderedCards()}
        </div>
        <div className="ui grid">
          <div className="ui row">
            <div className="eight wide column">
              <h2 className="ui header">Hot Connections</h2>
            </div>
            <div className="four wide column">
              <AttributeFilterModal label="Select Date Range" />
            </div>
            <div className="four wide column">
              <AttributeFilterModal label="Select Tracks" />
            </div>
          </div>
          {this.renderedConnections()}
        </div>
      </div>
    );
  }
}

const mapStatetoProps = (state) => {
  return {
    cards: state.backend.cards,
  };
};

export default connect(mapStatetoProps, { fetchCards })(HomePage);
