import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { ResultLines } from "../utils";
import { fetchRace, queryResults } from "../../actions";

import "../../styles/styles.css";

class RaceDetailPage extends React.Component {
  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.fetchRace(id);
    this.props.queryResults([
      {
        field: "race_id",
        operator: "eq",
        arguments: [id],
      },
    ]);
  }

  renderedClass(race_class) {
    let classText;

    switch (race_class) {
      case "mcl":
        classText = "Maiden Claiming";
        break;
      case "gstk":
        classText = "Graded Stakes";
        break;
      case "an2l":
        classText = "Allowance, Non-Winners of Two";
        break;
      case "clm":
        classText = "Claimimg";
        break;
      case "ocln":
        classText = "Allowance Optional Claiming Non-Winners of One";
        break;
      case "stk":
        classText = "Overnight Stakes";
        break;
      case "soc":
        classText = "Starter Allowance Optional Claiming";
        break;
      case "clmn":
        classText = "Claiming Non-Winners of One";
        break;
      case "str":
        classText = "Starter Allowance";
        break;
      case "msw":
        classText = "Maiden Special Weight";
        break;
      case "aoc":
        classText = "Allowance Optional Claiming";
        break;
      case "alw":
        classText = "Allowance";
        break;
      case "an3l":
        classText = "Allowance Non-Winners of Three";
        break;
      case "mdn":
        classText = "Maiden";
        break;
      default:
        classText = race_class;
    }
    return classText;
  }

  restrictionText(age, sex, state) {
    let ageText;
    let sexText;
    let stateText;
    switch (age) {
      case "31":
        ageText = "Three year olds and up";
        break;
      case "21":
        ageText = "Two year olds and up";
        break;
      case "3":
        ageText = "Three year olds";
        break;
      case "2":
        ageText = "Two year olds";
        break;
      default:
        ageText = age;
        break;
    }
    switch (sex) {
      case "o":
        sexText = ", open company";
        break;
      case "m":
        sexText = ", fillies and mares";
        break;
      case "f":
        sexText = ", fillies";
        break;
      default:
        sexText = `, ${sex}`;
        break;
    }
    if (!!state) {
      stateText = ", statebred";
    } else {
      stateText = "";
    }
    return ageText.concat(sexText, stateText);
  }

  renderedClaimPrice() {
    const race = this.props.race;
    if (!!race.claim_price) {
      return <div>Claim Price: {race.claim_price}</div>;
    } else {
      return "";
    }
  }

  renderedRaceInfo() {
    const race = this.props.race;
    if (race.track_code === undefined) {
      return null;
    }
    return (
      <div className="ui segment">
        <div>
          <h2 className="ui header center aligned">
            {race.track_code.toUpperCase() +
              " - " +
              race.date +
              " Race " +
              race.race_number}
          </h2>
        </div>
        <div>
          <div>Class: {this.renderedClass(race.classification)}</div>
          <div>Purse: {race.purse}</div>
          {this.renderedClaimPrice()}
          <div>
            Restrictions:{" "}
            {this.restrictionText(
              race.age_restrictions,
              race.sex_restrictions,
              race.is_state_bred
            )}
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div>
        <div>{this.renderedRaceInfo()}</div>
        <ResultLines
          headers={[
            "program_number",
            "horse_name",
            "trainer",
            "jockey",
            "owner",
          ]}
          results={this.props.results}
        />
      </div>
    );
  }
}

const mapStatetoProps = (state) => {
  return {
    race: state.backend.race,
    results: state.backend.results,
  };
};

export default connect(mapStatetoProps, { fetchRace, queryResults })(
  RaceDetailPage
);
