import React from "react";
import { connect } from "react-redux";

import { ResultLines } from "../utils";
import { fetchHorse, queryResults } from "../../actions";

import "../../styles/styles.css";

class HorseDetailPage extends React.Component {
  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.fetchHorse(id);
    this.props.queryResults([
      {
        field: "horse_id",
        operator: "eq",
        arguments: [id],
      },
    ]);
  }

  render() {
    return (
      <div>
        <div>
          <h2 className="ui header">{this.props.horse.name}</h2>
        </div>
        <ResultLines
          headers={[
            "track_code",
            "race_date",
            "race_number",
            "distance",
            "surface",
            "program_number",
            "trainer",
            "jockey",
            "owner",
          ]}
          results={this.props.results}
        />
      </div>
    );
  }
}

const mapStatetoProps = (state) => {
  return {
    horse: state.backend.horse,
    results: state.backend.results,
  };
};

export default connect(mapStatetoProps, { fetchHorse, queryResults })(
  HorseDetailPage
);
